.navbar{
  top: 0;
  position: sticky;
  margin: 0;
  padding: 0;
  width: 100%;
  height: 70px;
  background-color: rgb(19, 19, 19);
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.navbar__left__img{
  height: 73px;
  object-fit: contain;
  cursor: pointer;

}

.links{
  display: flex;
  align-items: center;
  text-decoration: none;
  margin-right: 50px;
}

.links a{
  text-decoration: none;
  color: white;
  margin-left: 20px;
  font-size: 25px;
}